// Advanced Client List with analytics and accessibility
import React, { useState } from 'react';

type FilterState = {
    status: string;
    financial: string;
    activity: string;
    tags: string[];
};
import { Client } from '../types/client';

interface ClientListProps {
    clients: Client[];
    onSelect: (client: Client) => void;
    onArchive: (clientId: string) => void;
    onUnarchive: (clientId: string) => void;
    selectedClientId?: string;
}

const ClientList: React.FC<ClientListProps> = ({
    clients,
    onSelect,
    onArchive,
    onUnarchive,
    selectedClientId,
}) => {
    const [selected, setSelected] = useState<string[]>([]);
    const [filters, setFilters] = useState<FilterState>({ status: '', financial: '', activity: '', tags: [] });
    const [searchTerm, setSearchTerm] = useState('');

    // Filter logic
    const filteredClients = clients.filter(client => {
        let match = true;
        if (filters.status && client.status !== filters.status) match = false;
        if (filters.financial === 'overdue' && (client.totalOwed || 0) <= 0) match = false;
        if (filters.financial === 'high-value' && (client.totalOwed || 0) < 5000) match = false;
        if (filters.activity === 'inactive' && (!client.lastInvoiceDate || new Date().getTime() - new Date(client.lastInvoiceDate).getTime() < 30 * 24 * 60 * 60 * 1000)) match = false;
        if (filters.tags.length > 0 && (!client.tags || !filters.tags.every(tag => client.tags?.includes(tag)))) match = false;
        if (searchTerm && searchTerm.length >= 2) {
            const term = searchTerm.toLowerCase();
            if (!(
                client.name?.toLowerCase().includes(term) ||
                client.email?.toLowerCase().includes(term) ||
                client.company?.toLowerCase().includes(term) ||
                client.phone?.toLowerCase().includes(term) ||
                client.poNumber?.toLowerCase().includes(term)
            )) match = false;
        }
        return match;
    });

    const handleSelect = (id: string) => {
        setSelected(selected.includes(id) ? selected.filter(s => s !== id) : [...selected, id]);
    };

    const handleBulkArchive = () => {
        selected.forEach(id => onArchive(id));
        setSelected([]);
    };

    // Accessibility: Announce errors for screen readers
    const [errorMsg, setErrorMsg] = useState('');

    // Mobile card view with swipe actions
    const isMobile = typeof window !== 'undefined' && window.innerWidth < 640;
    if (isMobile) {
        return (
            <div className="space-y-4">
                {/* Filter UI for mobile */}
                <div className="mb-2">
                    <input
                        type="text"
                        value={searchTerm}
                        onChange={e => setSearchTerm(e.target.value)}
                        placeholder="Search clients..."
                        className="w-full px-3 py-2 border rounded-lg mb-2"
                        aria-label="Search clients"
                    />
                    <select
                        value={filters.status}
                        onChange={e => setFilters(f => ({ ...f, status: e.target.value }))}
                        className="w-full px-3 py-2 border rounded-lg mb-2"
                        aria-label="Filter by status"
                    >
                        <option value="">All Statuses</option>
                        <option value="active">Active</option>
                        <option value="archived">Archived</option>
                        <option value="inactive">Inactive</option>
                    </select>
                    <select
                        value={filters.financial}
                        onChange={e => setFilters(f => ({ ...f, financial: e.target.value }))}
                        className="w-full px-3 py-2 border rounded-lg mb-2"
                        aria-label="Filter by financial status"
                    >
                        <option value="">All Financials</option>
                        <option value="overdue">Overdue</option>
                        <option value="high-value">High Value</option>
                    </select>
                </div>
                {filteredClients.map(client => (
                    <div
                        key={client.id}
                        className={`relative bg-white rounded-lg shadow p-4 flex flex-col gap-2 ${selectedClientId === client.id ? 'ring-2 ring-blue-500' : ''}`}
                        tabIndex={0}
                        aria-selected={selectedClientId === client.id}
                        style={{ minHeight: '48px' }}
                    >
                        <div className="flex justify-between items-center">
                            <div>
                                <div className="font-semibold text-gray-900">{client.name}</div>
                                <div className="text-sm text-gray-600">{client.email}</div>
                                <div className="text-xs text-gray-500">{client.company || '-'}</div>
                                <div className="text-lg font-bold text-blue-700">�a3{client.totalOwed?.toLocaleString() || '0'}</div>
                            </div>
                            <div className="flex gap-2">
                                <button
                                    onClick={() => onSelect(client)}
                                    className="px-2 py-1 text-xs bg-blue-100 text-blue-700 rounded hover:bg-blue-200 focus:ring-2 focus:ring-blue-500"
                                    aria-label={`View client ${client.name}`}
                                >
                                    View
                                </button>
                                {client.archived ? (
                                    <button
                                        onClick={() => onUnarchive(client.id)}
                                        className="px-2 py-1 text-xs bg-green-100 text-green-700 rounded hover:bg-green-200 focus:ring-2 focus:ring-green-500"
                                        aria-label={`Unarchive client ${client.name}`}
                                    >
                                        Unarchive
                                    </button>
                                ) : (
                                    <button
                                        onClick={() => onArchive(client.id)}
                                        className="px-2 py-1 text-xs bg-red-100 text-red-700 rounded hover:bg-red-200 focus:ring-2 focus:ring-red-500"
                                        aria-label={`Archive client ${client.name}`}
                                    >
                                        Archive
                                    </button>
                                )}
                            </div>
                        </div>
                        {/* Swipe actions: edit, archive, delete (simulate with buttons for now) */}
                        <div className="flex gap-2 mt-2">
                            <button
                                onClick={() => alert('Edit client feature coming soon')}
                                className="px-2 py-1 text-xs bg-yellow-100 text-yellow-700 rounded hover:bg-yellow-200 focus:ring-2 focus:ring-yellow-500"
                                aria-label={`Edit client ${client.name}`}
                            >
                                Edit
                            </button>
                            <button
                                onClick={() => alert('Delete client feature coming soon')}
                                className="px-2 py-1 text-xs bg-gray-100 text-gray-700 rounded hover:bg-gray-200 focus:ring-2 focus:ring-gray-500"
                                aria-label={`Delete client ${client.name}`}
                            >
                                Delete
                            </button>
                        </div>
                    </div>
                ))}
                {/* Accessibility: Error message for screen readers */}
                {errorMsg && <div role="alert" aria-live="assertive" className="sr-only">{errorMsg}</div>}
            </div>
        );
    }
    // Desktop table view
    return (
        <div className="overflow-x-auto">
            {/* Filter UI for desktop */}
            <div className="flex gap-4 mb-2 items-center">
                <input
                    type="text"
                    value={searchTerm}
                    onChange={e => setSearchTerm(e.target.value)}
                    placeholder="Search clients..."
                    className="px-3 py-2 border rounded-lg"
                    aria-label="Search clients"
                />
                <select
                    value={filters.status}
                    onChange={e => setFilters(f => ({ ...f, status: e.target.value }))}
                    className="px-3 py-2 border rounded-lg"
                    aria-label="Filter by status"
                >
                    <option value="">All Statuses</option>
                    <option value="active">Active</option>
                    <option value="archived">Archived</option>
                    <option value="inactive">Inactive</option>
                </select>
                <select
                    value={filters.financial}
                    onChange={e => setFilters(f => ({ ...f, financial: e.target.value }))}
                    className="px-3 py-2 border rounded-lg"
                    aria-label="Filter by financial status"
                >
                    <option value="">All Financials</option>
                    <option value="overdue">Overdue</option>
                    <option value="high-value">High Value</option>
                </select>
                {/* Add more filters as needed */}
            </div>
            <table role="grid" aria-label="Client list" className="min-w-full bg-white border rounded-lg">
                <thead className="bg-gray-50">
                    <tr>
                        <th className="px-4 py-2 text-left">
                            <input
                                type="checkbox"
                                checked={selected.length === filteredClients.length && filteredClients.length > 0}
                                onChange={e => setSelected(e.target.checked ? filteredClients.map(c => c.id) : [])}
                                aria-label="Select all clients"
                            />
                        </th>
                        <th className="px-4 py-2 text-left">Name</th>
                        <th className="px-4 py-2 text-left">Email</th>
                        <th className="px-4 py-2 text-left">Company</th>
                        <th className="px-4 py-2 text-left">Total Owed</th>
                        <th className="px-4 py-2 text-left">Last Invoice</th>
                        <th className="px-4 py-2 text-left">Status</th>
                        <th className="px-4 py-2 text-left">Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {filteredClients.map(client => (
                        <tr
                            key={client.id}
                            className={selectedClientId === client.id ? 'bg-blue-50' : ''}
                            tabIndex={0}
                            aria-selected={selectedClientId === client.id}
                            onClick={() => onSelect(client)}
                            onKeyDown={e => {
                                if (e.key === 'Enter' || e.key === ' ') onSelect(client);
                            }}
                            style={{ outline: selectedClientId === client.id ? '2px solid #2563eb' : 'none' }}
                        >
                            <td className="px-4 py-2">
                                <input
                                    type="checkbox"
                                    checked={selected.includes(client.id)}
                                    onChange={e => {
                                        e.stopPropagation();
                                        handleSelect(client.id);
                                    }}
                                    aria-label={`Select client ${client.name}`}
                                />
                            </td>
                            <td className="px-4 py-2 font-semibold text-gray-900">{client.name}</td>
                            <td className="px-4 py-2 text-gray-600">{client.email}</td>
                            <td className="px-4 py-2 text-gray-500">{client.company || '-'}</td>
                            <td className="px-4 py-2 text-right text-gray-900">�a3{client.totalOwed?.toLocaleString() || '0'}</td>
                            <td className="px-4 py-2 text-gray-600">{client.lastInvoiceDate ? new Date(client.lastInvoiceDate).toLocaleDateString() : '-'}</td>
                            <td className="px-4 py-2">
                                <span className={`px-2 py-1 rounded text-xs font-semibold ${client.status === 'archived' ? 'bg-gray-200 text-gray-700' : client.status === 'active' ? 'bg-green-100 text-green-700' : 'bg-yellow-100 text-yellow-700'}`}>
                                    {client.status || (client.archived ? 'archived' : 'active')}
                                </span>
                            </td>
                            <td className="px-4 py-2 flex gap-2">
                                {client.archived ? (
                                    <button
                                        onClick={e => {
                                            e.stopPropagation();
                                            onUnarchive(client.id);
                                        }}
                                        className="px-2 py-1 text-xs bg-green-100 text-green-700 rounded hover:bg-green-200 focus:ring-2 focus:ring-green-500"
                                        aria-label={`Unarchive client ${client.name}`}
                                    >
                                        Unarchive
                                    </button>
                                ) : (
                                    <button
                                        onClick={e => {
                                            e.stopPropagation();
                                            onArchive(client.id);
                                        }}
                                        className="px-2 py-1 text-xs bg-red-100 text-red-700 rounded hover:bg-red-200 focus:ring-2 focus:ring-red-500"
                                        aria-label={`Archive client ${client.name}`}
                                    >
                                        Archive
                                    </button>
                                )}
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            {selected.length > 0 && (
                <div className="mt-4 flex gap-2">
                    <button
                        onClick={handleBulkArchive}
                        className="px-4 py-2 bg-red-600 text-white rounded-lg font-semibold hover:bg-red-700"
                        aria-label="Archive selected clients"
                    >
                        Archive Selected
                    </button>
                    {/* Add more bulk actions here */}
                </div>
            )}
            {/* Accessibility: Error message for screen readers */}
            {errorMsg && <div role="alert" aria-live="assertive" className="sr-only">{errorMsg}</div>}
        </div>
    );
};

export default ClientList;
